}
.body{
  background-color: #282c34;

}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header-centered {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  flex-direction: column;
}


.App-link {
}
.bg-darkgray{
  color: darkgrey;

}


.table td.fit,
.table th.fit {
  white-space: nowrap;
  width: 1%;
}

.card{
  transition: all 0.2s;
  transition-delay:0.2s
}

.other-card {
  composes: card;
  transition: all 0.1s;
  transition-delay:0.1s;
  cursor: pointer
}

.other-card:hover {
  transform: scale(1.05)
}



